import { Button, Form, Input, Modal } from "antd";
import { useForm } from "antd/es/form/Form";
import { TaskSelect } from "components/task-select";
import { UserSelect } from "components/user-select";
import React from "react";
import { useEffect } from "react";
import { useDeleteTask, useEditTask } from "utils/project";
import { useTaskModal, useTasksQueryKey } from "./utils";

const layout = {
    labelCol:{span:8},
    wrapperCol:{span:16}
}

export const TaskModal = ()=>{
    const [form]=useForm()
    const {editingTaskId,editingTask,close} = useTaskModal()
    const {mutateAsync:editTask,isLoading:editLoading} = useEditTask(useTasksQueryKey())
    const {mutate:deleteTaks} = useDeleteTask(useTasksQueryKey())

    const onCancel = ()=>{
        close()
        form.resetFields() //表单重置
    }

    const onOk = async ()=>{
        await editTask({...editingTask,...form.getFieldsValue()})
        close()
    }


    const startEdit = ()=>{
        close()
        Modal.confirm({
            okText:'确定',
            cancelText:'取消',
            title:'确定删除看板吗'
            ,onOk(){
                deleteTaks({id:Number(editingTaskId)})
            }
        })
    }

    useEffect(()=>{
        form.setFieldsValue(editingTask)
    },[form,editingTask])

    return <Modal
    forceRender
    okText={'确认'}
    onCancel={onCancel}
    onOk={onOk}
     cancelText={'取消'}
      confirmLoading={editLoading} title={'编辑'} 
      visible={!!editingTaskId}  >
          <Form {...layout} initialValues={editTask} form={form} >
              <Form.Item label={'任务名'} name={'name'} rules={[{required:true,message:'请输入人物名'}]}>
                  <Input/>
              </Form.Item>
              <Form.Item label={'经办人'} name={'processorId'}>
                  <UserSelect defaultOptionName="经办人" />
              </Form.Item>
              <Form.Item label={'类型'} name={'typeId'}>
              <TaskSelect/>
              </Form.Item>
              <Button onClick={startEdit} >
                  删除
              </Button>
          </Form>
    </Modal>

}