import { Card, Input } from 'antd'
import React from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import { useAddTask } from 'utils/project'
import { useProjectIdInUrl, useProjectInUrl, useTasksQueryKey } from './utils'

export const CreateTask=({kanbanId}:{kanbanId:number})=> {
    const [name,setName] = useState('')
    const [inputMode,setInputMode] = useState(false)
    const {mutateAsync:addTask} = useAddTask(useTasksQueryKey())
    const projectId = useProjectIdInUrl()
    const submit = async ()=>{
        await addTask({projectId,name,kanbanId})
        setInputMode(false)
        setName('')
    }
    const toggle = ()=>setInputMode(mode=>!mode)

    useEffect(()=>{
        if (!inputMode) {
            setName('')
        }
    },[inputMode])

    if (!inputMode) {
        return <div onClick={toggle} >创建事务</div>
    }

    return (
        <Card>
            <Input onBlur={toggle} placeholder={'需要做些什么'} 
            value={name}
            autoFocus={true} //文本输入字段被设置为当页面加载时获得焦点：
            onPressEnter={submit} //回车事件
            onChange={e=>setName(e.target.value)}
            ></Input>
        </Card>
    )
}


