import { Button, List} from "antd";
import { Row } from "components/lib";
import dayjs from "dayjs";
import React from "react";
import { Link } from "react-router-dom";
import { useProjectInUrl } from "screens/kanban/utils";
import { useDeleteEpic, useEpics } from "utils/epic";
import { useTask } from "utils/task";
import { useEpicQueryKey, useEpicSearchParams } from "./utils";

export const EpicScreen = () => {

  const {data:currentProject} = useProjectInUrl()
  const {data:epics} = useEpics() 
  const {data:tasks} = useTask({projectId:currentProject?.id})
  const {mutate:deleteEpic} = useDeleteEpic(useEpicQueryKey())
  return (
    <div className="Kanban"  >
    <h1>{currentProject?.name}任务组</h1>
    <List dataSource={epics} itemLayout={'vertical'} renderItem={epic=>{
      return (
        <List.Item>
          <List.Item.Meta title={<Row between ><span> {epic.name} </span>
           <Button type="link"  >删除</Button>
           </Row>
        } 
        description={
          <div>
            <div>开始时间：{dayjs(epic.start).format("YYYY-MM-DD")} </div>
            <div>结束时间：{dayjs(epic.end).format("YYYY-MM-DD")}</div>
          </div>
        }
          />
          <div>
            {tasks?.filter(task=>task.epicId===epic.id).
            map(task=><Link to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`} key={task.id}  >{task.name}</Link>)}
          </div>
        </List.Item>

      )}
    } />
    </div>
  )
  
  
};
