import Item from "antd/lib/list/Item";
import React from "react";



const inserAfter = (list:unknown[],from:number,to:number)=>{
    const toItem = list[to]
    const removedItem = list.splice(from,1)[0]
    const toIndex = list.indexOf(toItem)
    list.splice(toIndex+1,0,removedItem)
    return list
}
const inserBefore = (list:unknown[],from:number,to:number)=>{
    const toItem = list[to]
    const removedItem = list.splice(from,1)[0]
    const toIndex = list.indexOf(toItem)
    list.splice(toIndex+1,0,removedItem)
    return list
}
export const reorder = ({fromId,type,refercenceId,list}
    :{list:{id:number}[],fromId:number,type:'after' | 'before' ,refercenceId:number }
    )=>{
        const copiedList = [...list]
        const movingItemIndex = copiedList.findIndex((item:{id:number})=>(item.id===fromId))

        if (!refercenceId) {
            return inserAfter([...copiedList],movingItemIndex,copiedList.length-1)
        }
        const targetIndex = copiedList.findIndex((item:{id:number})=>item.id === refercenceId )
        const insert = type === 'after' ? inserAfter : inserBefore
        return insert([...copiedList],movingItemIndex,targetIndex)

}