import { useCallback, useMemo } from "react"
import { useLocation } from "react-router"
import { useDebounce } from "utils"
import { useProject, useTaks } from "utils/project"
import { useUrlQueryParam } from "utils/url"

export const useProjectIdInUrl = ()=>{
    const {pathname} = useLocation()
    const id = pathname.match(/projects\/(\d+)/)?.[1] 
    return Number(id)
} 

export const useProjectInUrl = ()=>useProject(useProjectIdInUrl())

export const useKanbanSearchParams = ()=>({projectId:useProjectIdInUrl()})

export const useKanbanQueryKey = ()=>['kanbans',useKanbanSearchParams()]

export const useTasksSearchParams = ()=>{
   const [param,setParam] = useUrlQueryParam([
       'name',
       'typeId',
       'processorId',
       'tagId'
   ])
   const projectId = useProjectIdInUrl()
   const Dname = useDebounce(param.name,200)
   return useMemo(()=>({
       projectId
       ,typeId:Number(param.typeId) || undefined
       ,processorId:Number(param.processorId)||undefined
       ,tagId:Number(param.tagId) || undefined
       ,name:param.name || undefined
}),[projectId,param])
}

export const useTasksQueryKey = ()=>['tasks',useKanbanSearchParams()]

export const useTaskModal = ()=>{
    const [{editingTaskId},setEditingTaskId] = useUrlQueryParam(['editingTaskId'])
    const {data:editingTask,isLoading} = useTaks(Number(editingTaskId))
    const statrEdit = useCallback((id:number)=>{
        setEditingTaskId({editingTaskId:id})
    },[setEditingTaskId])
    const close = useCallback(()=>{setEditingTaskId({editingTaskId:''})},[setEditingTaskId])
    return{
        editingTaskId,
        editingTask,
        statrEdit,
        close,
        isLoading
    }

}