import { QueryKey, useMutation, useQuery } from "react-query";
import { Kanban } from "types";
import { useHttp } from "./http";
import { useReorderConfig, useReorderKanbanConfig, useReorderTaksConfig } from "./use-options";


export const useKanbans = (param?:Partial<Kanban>)=>{
    const client = useHttp()

    return useQuery<Kanban[]>(['kanbans',param],()=>{
       return client('kanbans',{data:param})
    })

}

export interface SortProps{
    //提起来的
    fromId?:number
    // 目标item
    referenceId:number
    //前还是后
    type:'before' | 'after'
    fromkanbanId?:number
    toKanbanId?:number
}

export const useReorderKanban = (querKey:QueryKey)=>{
    const client = useHttp()
    
    return useMutation(
        (params:SortProps)=>{
            return client('kanbans/reorder',{
                data:params,
                method:'POST'
            })
        }
        ,useReorderKanbanConfig(querKey)
    )



}
export const useReorderTask = (querKey:QueryKey)=>{
    const client = useHttp()

    return useMutation(
        (params:SortProps)=>{
            return client('tasks/reorder',{
                data:params,
                method:'POST'
            })
        }
        ,useReorderTaksConfig(querKey)
    )

}