import React from "react";
import { PropsWithChildren } from "react";
import { ReactNode } from "react";
import {Droppable, Draggable,DroppableProps, DroppableProvided, DroppableProvidedProps, DraggableProps } from "react-beautiful-dnd"

// isValidElement
// forwardRef
// HTMLAttributes

type DropProps = Omit<DroppableProps,'children'>  & {children:ReactNode}

export const Drop = ({children,...props}:DropProps) => {
  return (
    <Droppable {...props} >

      {provided=>{
        if (React.isValidElement(children)) {          
          return React.cloneElement(children,{
            ...provided.droppableProps,
            ref:provided.innerRef,
            provided
          })
        }
        return <div ></div>
      }}

      </Droppable>
  )
}

type DroChildProps = Partial<{provided:DroppableProvided} 

& DroppableProvidedProps & React.HTMLAttributes<HTMLElement> >

export const DropChild = React.forwardRef<HTMLDivElement,DroChildProps>(
  ({children,...props},ref)=>{ 
  return (<div ref={ref} {...props}>
    {children}
    {props.provided?.placeholder} 
    </div>)
})



type DragProps = Omit<DraggableProps,'children'>  & {children:ReactNode}

export const Drag = ({children,...props}:DragProps)=>{
  return <Draggable {...props} >
    {
      provided=>{
        if (React.isValidElement(children)) {
          return React.cloneElement(children,{
            ...provided.draggableProps,
            ...provided.dragHandleProps,
            ref:provided.innerRef 
          })
        }

        return <div className="hihlli" ></div>
      }
    }
    </Draggable>
}