import styled from "@emotion/styled";
import React, { useState } from "react";
import { Kanban } from "types";
import { useTask } from "utils/task";
import { useTaskTypes } from "utils/task-type";
import { useKanbanQueryKey, useTaskModal, useTasksSearchParams } from "./utils";
import taskIcon from 'assets/task.svg'
import bugIcon from 'assets/bug.svg' 
import { Button, Card, Dropdown, Menu, Modal } from "antd";
import { CreateTask } from "./create-task";
import { Mark } from "components/mark";
import { useDeleteKanban } from "utils/project";
import { Row } from "components/lib";
import { useSearchParams } from "react-router-dom";
import { Drag, Drop, DropChild } from "components/drag";

export interface Task{
    id:number
    name:string
    //经办人
    processorId:number
    projectId:number
    //任务组
    epicId:number
    kanbanId:number
    //bug or task
    typeId:number
    note:string
}

const TaskTypeIcon = ({id}:{id:number})=>{
    const {data:taskTypes} = useTaskTypes()
    
    const name = taskTypes?.find(taskTypes=>taskTypes.id===id)?.name
    if (!name) {
        return null
    }
    return <img src={name==='task'?taskIcon:bugIcon}/>
}

export const TaskCard = ({task}:{task:Task})=>{
    const {statrEdit}=useTaskModal()
    const {name:KeyWord} = useTasksSearchParams()
    return(
        <div className="KanbanCard" onClick={()=>statrEdit(task.id)} key={task.id}>
        <div><Mark keyWord={KeyWord} name={task.name} /></div>
        <TaskTypeIcon  id={task.typeId}/>
        </div>
    )
    
}




const More = ({kanban}:{kanban:Kanban})=>{
    const {mutateAsync} = useDeleteKanban(useKanbanQueryKey())

    const startEdit = ()=>{
        Modal.confirm({
            okText:'确定',
            cancelText:'取消',
            title:'确定删除看板吗'
            ,onOk(){
                mutateAsync({id:kanban.id})
            }
        })
    }
    const overlay = <Menu>
        <Menu.Item>
            <Button type="link" onClick={startEdit} >
                删除
            </Button>
        </Menu.Item>
    </Menu>;

return (
<Dropdown overlay={overlay}>
    <Button type="link" >...</Button>
</Dropdown>)

}


export const KanbanColumn = React.forwardRef
<HTMLDivElement,{kanban:Kanban}>
(({kanban:kanban,...props},ref)=>{
    
    const {data:allTasks} = useTask(useTasksSearchParams())
    const tasks = allTasks?.filter(task=>task.kanbanId===kanban.id)
    return(
            <Container {...props} ref={ref} >
            <Row between  >
            <h3 style={{fontWeight:'bold'}} > {kanban.name} </h3>
            <More kanban={kanban} key={kanban.id} ></More>
            </Row>
            
            <Drop type={'ROW'} direction='vertical' droppableId={String(kanban.id)} >
                <DropChild style={{minHeight:'1rem'}} >
            {tasks?.map((task,index)=>
            <Drag key={task.id} index={index} draggableId={'task'+task.id} >
                <div>
                   <TaskCard key={task.id} task={task} /> 
                   </div>
            </Drag>
            )} 
            </DropChild>
            </Drop>

            <div className="KanbanCard">
            <CreateTask kanbanId={kanban.id}></CreateTask>
            </div>
            
            </Container>
        
    )
})



export const Container = styled.div`
  min-width: 27rem;
  border-radius: 6px;
  background-color: rgb(244, 245, 247);
  display: flex;
  flex-direction: column;
  padding: 0.7rem 0.7rem 1rem;
  margin-right: 1.5rem;
`;