import { Input } from "antd";
import React from "react";
import { useState } from "react";
import { useKanbans } from "utils/kanban";
import { useAddKanban, useAddProject } from "utils/project";
import { useKanbanQueryKey, useProjectIdInUrl } from "./utils";

export const CreateKanban = ()=>{
    const[name,setName] = useState('')
    const projectId = useProjectIdInUrl() 
    const {mutateAsync:addKanban} = useAddKanban(useKanbanQueryKey())

    const submit = async()=>{
        await addKanban({name,projectId})
        setName('')
    }

    return (
        <div>
            <Input size={"large"} placeholder="新建看板名称"
             onPressEnter={submit} value={name} 
             onChange={e=>setName(e.target.value)
             }/>
        </div>
        )
}