import { Button, Input } from "antd";
import { Row } from "components/lib";
import { TaskSelect } from "components/task-select";
import { UserSelect } from "components/user-select";
import React from "react";
import { useSetUrlSearchParam } from "utils/url";
import { useTasksSearchParams } from "./utils";

function SearchKanban() {
    const searchParams = useTasksSearchParams()
    const setSearchParams = useSetUrlSearchParam()
    const reset = ()=>{
        setSearchParams({
        typeId: undefined
       ,processorId:undefined
       ,tagId: undefined
       ,name: undefined
        })
    }
    return (
        <Row marginBottom={3} gap={true} >
            <Input onChange={e=>setSearchParams({name:e.target.value})}
            style={{width:'20rem'}} placeholder="任务名" value={searchParams.name} ></Input>
            <UserSelect defaultOptionName="经办人" value={searchParams.processorId}
            onChange={value=>setSearchParams({processorId:value})}
            ></UserSelect>
            <TaskSelect defaultOptionName="类型" value={searchParams.typeId}
            onChange={value=>setSearchParams({typeId:value})}
            ></TaskSelect>
            <Button onClick={reset} >清除筛选器</Button>
        </Row>
    )
}

export default SearchKanban
