import {QueryKey, useQueryClient} from 'react-query'
import { Task } from 'types'
import { reorder } from './Reorder'

export const useConfig = (querykey:QueryKey,callback:(target:any,old?:any[])=>any[])=>
{
    const queryClient = useQueryClient()
    return {
     onSuccess:()=>queryClient.invalidateQueries(querykey)
     //乐观更新
     ,async onMutate(target:any){
       const previousItems = queryClient.getQueryData(querykey)
       queryClient.setQueryData(querykey,(old?:any[])=>{
           return callback(target,old)
       }
       )
       return {previousItems}
     },onError(error:any,newItem:any,context:any){
       queryClient.setQueryData(querykey,context.previousItems)
     }
    }
} 

export const useDeleteConfig = (querykey:QueryKey) =>useConfig(querykey,
(target,old:any[]|undefined)=>old?.filter(item=>item.id !== target.id)||[])

export const useEditConfig = (querykey:QueryKey) =>useConfig(querykey,
(target,old:any[]|undefined)=>old?.map(item=>item.id === target.id?{...item,...target}:item)||[])

export const useAddConfig = (querykey:QueryKey) =>useConfig(querykey,
(target,old:any[]|undefined)=> old ? [...old,target] : [])

export const useReorderConfig = (querykey:QueryKey)=>{
  return useConfig(querykey,(target,old)=>old || [] )
}    

export const useReorderKanbanConfig = (querykey:QueryKey)=>{
  return useConfig(querykey,(targer,old)=>reorder({list:old,...targer}))
}  

export const useReorderTaksConfig = (queryKey:QueryKey)=>
useConfig(queryKey,(target,old)=>{

  const orderedList = reorder({list:old,...target} )as Task[] 

  return orderedList.map((item:Task)=>
  item.id === target.fromId ? {...item,kanbanId:target.toKanbanId} : item
  )
})