import React from "react";
import { User } from "screens/project-list/search-panel";
import { Button, Dropdown, Menu, Modal, Table } from "antd";
import dayjs from "dayjs";
import { TableProps } from "antd/es/table";
// react-router 和 react-router-dom的关系，类似于 react 和 react-dom/react-native/react-vr...
import { Link } from "react-router-dom";
import { Pin } from "components/pin";
import { useDeleteProject, useEditProject, useProjectModal, useProjects } from "utils/project";
import { ButtonNoPadding } from "components/lib";
import { useProjectQueryKey } from "./util";

// TODO 把所有ID都改成number类型


interface ListProps extends TableProps<Project> {
  users: User[]
  refresh?: () => void
}
  
export interface Project {
    id: number
    name: string
    personId: number
    pin: boolean
    organization: string
    created: number
  }
   
interface ListProps extends TableProps<Project> {
    users: User[]
    // refresh?: () => void //星星刷新
  }


  
export const List = ({ users, ...props }: ListProps) => {
     const {mutate} = useEditProject(useProjectQueryKey()) 
     const pinProject = (id:number)=>(pin:boolean)=>mutate({id,pin})
     const {statrEdit} = useProjectModal() 
     const editProject = (id:number)=>()=>statrEdit(id)   
     
const More = ({ project }: { project: Project }) => {

  //删除
  const {mutate:deletePorjects} = useDeleteProject(useProjectQueryKey())
  const DeleteProject = (id:number)=>{
    Modal.confirm({
      title:'确定删除吗',
      content:'点击删除',
      okText:'确定',
      onOk(){
        deletePorjects({id})
      }
    })
  }
  
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item onClick={editProject(project.id)} key={"edit"}>
           编辑
          </Menu.Item>
          <Menu.Item onClick={()=>DeleteProject(project.id)} key={"delete"}>
            删除
          </Menu.Item>
        </Menu>
      }
    >
      <ButtonNoPadding type={"link"}>...</ButtonNoPadding>
    </Dropdown>
  );
};   
     
     return ( <Table pagination={false} rowKey={"id"} columns={[

         //星星 checked控制是否点亮，disabled控制是否可用
         {
         title:<Pin checked disabled />,
       render(value,project){
        return <Pin checked={project.pin} onCheckedChange={pinProject(project.id)} />
         }
     },
        
         {
          title:'项目名'
          ,sorter:(a,b) => a.name.localeCompare(b.name) //localeCompare可以排序中文字符
          ,render(value,project){
             return <Link to={String('/projects/'+ project.id+'/kanban')}>{project.name}</Link>;
          } 
          },
        {
           title:'部门'
           ,dataIndex:'organization'
           ,sorter:(a,b) => a.name.localeCompare(b.name) //localeCompare可以排序中文字符
           },

          {title:'负责人'
          ,render(_value,project){
          return <span>
         {users.find(users=>users.id===project.personId)?.name || '未知'}

        </span> 
         }
        },    
      {
        render(value, project) {
          return <More project={project} />;
        },
      },
    
     ]}  {...props} >
         
         </Table>
     )
 }

