import { URLSearchParamsInit, useSearchParams } from "react-router-dom";
import { useMemo, useState } from "react";
import { cleanObject, subset } from "utils/index";

/**
 * 返回页面url中，指定键的参数值
 */
 export const useUrlQueryParam = <K extends string>(keys: K[]) => {
  const [searchParams] = useSearchParams(); //自带的读取url信息的API
  const setSearchParams = useSetUrlSearchParam();
  const [stateKeys] = useState(keys);
  return [
    useMemo(
      () =>
      subset(Object.fromEntries(searchParams), stateKeys)                as {[key in K]: string;},
      [searchParams, stateKeys]      
    ),
    (params: Partial<{ [key in K]: unknown }>) => {return setSearchParams(params);},
  ] as const;
  //return [ subset({name:jdald,project:jdsldj}), [[name,jdald],[project:jdsldj]] ]
};


export const useSetUrlSearchParam = () => {
  const [searchParams, setSearchParam] = useSearchParams();
  return (params: { [key in string]: unknown }) => {
    const o = cleanObject({
      ...Object.fromEntries(searchParams), //把数组传为对象
      ...params,
    }) as URLSearchParamsInit;
    return setSearchParam(o);
  };
};