export const useProject = (id?:number)=>{

  const client = useHttp()
  return useQuery<Project>(['project',{id}],
  ()=>client(`projects/${id}`)
  ,{
    enabled:Boolean(id)
    //id存在才触发useQuery
  }
  )
  
}

export const useTaks = (id?:number)=>{

  const client = useHttp()
  return useQuery<Project>(['task',{id}],
  ()=>client(`tasks/${id}`)
  ,{
    enabled:Boolean(id)
  }
  )
  
}



// url管理打开关闭模块
export const  useProjectModal = () => {
  const [{ projectModalOpen}, setProjectModalOpen] = useUrlQueryParam([
    'projectModalOpen',
  ]);
  const [{editingProjectId},setEditingProjectId] = useUrlQueryParam([
    'editingProjectId'
  ])
  const setUrlParams = useSetUrlSearchParam();
  const {data:editingProject,isLoading} = useProject(Number(editingProjectId)) 
  const open = () => setProjectModalOpen({ projectModalOpen: true });

  // const close = () => 
  // setProjectModalOpen({ projectModalOpen:undefined})
  // setEditingProjectId({editingProjectId:undefined})

  const close = () => setUrlParams({ projectModalOpen: "", editingProjectId: "" });

  const statrEdit = (id:number) => setEditingProjectId({editingProjectId:id})
  return {
    projectModalOpen:projectModalOpen === 'true' || Boolean(editingProjectId) ,
    open,
    close,
    editingProject,
    isLoading,
    statrEdit
  }
};
