import styled from "@emotion/styled";
import React from "react";
import { useDocumentTitle } from "utils";
import { useKanbans, useReorderKanban, useReorderTask } from "utils/kanban";
import { KanbanColumn } from "./kanban-column";
import { useKanbanQueryKey, useKanbanSearchParams, useProjectInUrl, useTasksQueryKey, useTasksSearchParams } from "./utils";
import './index.css'
import SearchKanban from "./SearchKanban";
import { useTask } from "utils/task";
import { Spin } from "antd";
import { CreateKanban } from "./create-kanban";
import { TaskModal } from "./task-modal";
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { Drag, Drop, DropChild } from "components/drag";
import { useSearchParams } from "react-router-dom";
import { useCallback } from "react";



export const KanbanScreen = () => {
  useDocumentTitle('看板列表')
  const {data:currentProject} = useProjectInUrl()
  const {data:kanbans,isLoading:kanbanIsloding} = useKanbans(useKanbanSearchParams())
  const {isLoading:taskIsLoading}= useTask(useTasksSearchParams())
  const isLoading = taskIsLoading || kanbanIsloding


  const onDragEnd  = useDarpEnd()


  return (
    <DragDropContext onDragEnd={onDragEnd} >
    <div className="Kanban" >
      {/* <CreateKanban/> */}
    <h1>{currentProject?.name}看板</h1>
    <SearchKanban/>
    <CreateKanban/>
    <br />
    {isLoading ? <Spin size={"large"}/>
     :
    <Drop type={'COLUMN'} direction={'horizontal'} droppableId={'kanban'} >
    
      {/* 以下是api */}
  
    <DropChild style={{display:"flex",overflow:'auto'}} >
    {kanbans?.map((kanban,index)=>
    <Drag key={kanban.id} draggableId={'kanban'+kanban.id} index={index} >
      <KanbanColumn kanban={kanban} key={kanban.id} />
    </Drag>)}
    </DropChild>

    </Drop>
    
    }
    <TaskModal/>
    </div>
    </DragDropContext>
  )
  

};

export const useDarpEnd = ()=>{
  const {data:kanbans} = useKanbans(useKanbanSearchParams())
  const {mutate:Kanbanmutate} = useReorderKanban(useKanbanQueryKey())
  const {mutate:Taksmutate} = useReorderTask(useTasksQueryKey())
  const {data:allTasks} = useTask(useTasksSearchParams())
  
  
  return useCallback(
    ({source,destination,type}:DropResult)=>{
      if (!destination) {  
        return
      }
      if (type==='COLUMN') {
        const fromId = kanbans?.[source.index].id
        const toId = kanbans?.[destination.index].id as number
        const type = destination.index > source.index ? 'after' : 'before'
        Kanbanmutate({fromId,referenceId:toId,type});
      }

        
   if (type==='ROW') {
     const fromkanbanId = +source.droppableId
     const toKanbanId = +destination.droppableId
     if (fromkanbanId === toKanbanId) {
       return  
     }
     const fromTaks:any = allTasks?.filter(task=>task.kanbanId===fromkanbanId)[source.index] 
     const toTask:any = allTasks?.filter(task=>task.kanbanId===toKanbanId)[destination.index]
     if ( fromTaks?.id===toTask?.id) {
       return
     }
     Taksmutate({
       fromId:fromTaks?.id ,
       referenceId:toTask?.id,
       fromkanbanId,
       toKanbanId,
       type:fromkanbanId===toKanbanId && destination.index > source.index ?  'after' : 'before'
     })
   }
    }
  ,[kanbans,Kanbanmutate,allTasks,Taksmutate])

}